import { useState } from "react";
import { myProjects } from "../../globals/constants";
import { ProjectContainer } from "./ProjectContainer";
import "./style.scss";
import ImageFollower from "../MyProjects/ImageFollower";

export function MyProjectsNew() {
  const [choosenProject, setChoosenProject] = useState(0);
  const [hoveredImage, setHoveredImage] = useState<string[]>([]);


  return (
    <>
      <ImageFollower projectImages={hoveredImage} />
      <span id="projects"></span>
      <div className="my-projects change-color">
        {myProjects.map((project) => (
          <ProjectContainer
            key={project.id}
            project={project}
            setHoveredImage={setHoveredImage}
            onClick={() => {
              setChoosenProject(project.id);
            }}
            isChosen={project.id === choosenProject}
            rotation={(project.id - choosenProject) * (50 / myProjects.length)}
          />
        ))}
      </div>
    </>
  );
}
